import React, { useEffect, useState } from "react";
import { Layout, Typography, List, Card, Button, Modal } from "antd";
import "antd/dist/reset.css";
import Navbar from "./Navbar";

const { Title, Text } = Typography;

const MyTicket = () => {
    // State to hold the tickets and the modal visibility state
    const [tickets, setTickets] = useState([]);
    const [isModalVisible, setIsModalVisible] = useState(false);
    const [selectedTicket, setSelectedTicket] = useState(null);

    useEffect(() => {
        // Lấy dữ liệu vé từ localStorage
        const savedTickets = JSON.parse(localStorage.getItem("tickets")) || [];
        setTickets(savedTickets);
    }, []);

    const handleShowDetails = (ticket) => {
        setSelectedTicket(ticket);
        setIsModalVisible(true);
    };

    const handleCloseModal = () => {
        setIsModalVisible(false);
        setSelectedTicket(null);
    };

    return (
        <Layout>
            <Navbar />
            <Layout.Content style={{ padding: "24px" }}>
                <Title level={2}>Vé của tôi</Title>
                {tickets.length === 0 ? (
                    <Text>Không có vé nào.</Text>
                ) : (
                    <List
                        grid={{ }}
                        dataSource={tickets}
                        renderItem={(ticket) => (
                            <List.Item>
                                <Card
                                    style={{ width: "fit-content", backgroundColor:"#FFCC66" }}
                                    hoverable
                                >
                                    <p>
                                        <b>Địa điểm:</b> {ticket.location}
                                    </p>
                                    <p>
                                        <b>Ngày checkin:</b>{" "}
                                        {ticket.checkinDate}
                                    </p>
                                    <Button
                                        type="primary"
                                        onClick={() =>
                                            handleShowDetails(ticket)
                                        }
                                    >
                                        Xem chi tiết
                                    </Button>
                                </Card>
                            </List.Item>
                        )}
                    />
                )}
            </Layout.Content>

            {/* Modal for showing ticket details */}
            <Modal
                title="Chi tiết vé"
                visible={isModalVisible}
                onCancel={handleCloseModal}
                footer={[
                    <Button key="close" onClick={handleCloseModal}>
                        Đóng
                    </Button>,
                ]}
            >
                {selectedTicket && (
                    <>
                        <p>
                            <b>Địa điểm:</b> {selectedTicket.location}
                        </p>
                        <p>
                            <b>Ngày checkin:</b> {selectedTicket.checkinDate}
                        </p>
                        <p>
                            <b>Số vé người lớn:</b>{" "}
                            {selectedTicket.adultTickets}
                        </p>
                        <p>
                            <b>Số vé học sinh(sinh viên):</b>{" "}
                            {selectedTicket.studentTickets}
                        </p>
                        <p>
                            <b>Mã đơn hàng:</b> {selectedTicket.orderCode}
                        </p>
                    </>
                )}
            </Modal>
        </Layout>
    );
};

export default MyTicket;
