import React, { useState } from "react";
import { Card, Button, Row, Modal } from "antd";
import { ButtonAmt } from "./ButtonAmt";
import CheckinDatePicker from "./DatePicker";
import { notification } from "antd";
import { useNavigate } from "react-router-dom"; // Sử dụng cho điều hướng

const Register = ({
    location,
    checkinDate,
    locationDetails,
    setCheckinDate,
    codedh,
}) => {
    const [cnt1, setCnt1] = useState(0);
    const [cnt2, setCnt2] = useState(0);
    const [isModalVisible, setIsModalVisible] = useState(false);
    const navigate = useNavigate();

    const openNotification = (msg) => {
        notification.open({
            message: "Thông báo",
            description: msg,
            duration: 3,
        });
    };

    const showNotification = () => {
        setIsModalVisible(true);
    };

    const handleOk = () => {
        console.log("Đã xác nhận");

        // Tạo đối tượng vé mới
        const newTicket = {
            location: locationDetails[location],
            checkinDate,
            adultTickets: cnt1,
            studentTickets: cnt2,
            price: cnt1 * 160000 + cnt2 * 80000,
            orderCode: codedh,
        };

        // Lấy mảng vé hiện tại từ localStorage (nếu có)
        let tickets = JSON.parse(localStorage.getItem("tickets")) || [];

        // Thêm vé mới vào mảng
        tickets.push(newTicket);

        // Lưu lại mảng vé vào localStorage
        localStorage.setItem("tickets", JSON.stringify(tickets));

        // Đóng modal sau khi lưu
        setIsModalVisible(false);
        navigate("/my-tickets");
    };

    const handleCancel = () => {
        console.log("Đã hủy");
        setIsModalVisible(false);
    };

    const checkingData = () => {
        if (!checkinDate) {
            openNotification("Vui lòng chọn ngày checkin!");
        } else if (cnt1 + cnt2 == 0) {
            openNotification("Vui lòng chọn loại vé!");
        } else {
            showNotification();
        }
    };

    return (
        <>
            {location && (
                <Card hoverable style={{ width: "90%", marginTop: "15px" }}>
                    <h1>TÙY CHỌN GÓI DỊCH VỤ</h1>
                    <b>
                        Chọn gói dịch vụ cho {locationDetails[location]} (trẻ em
                        dưới 5 tuổi được miễn phí vé)
                    </b>
                    <div style={{ marginBottom: "20px" }}>
                        <CheckinDatePicker
                            onChange={(date) => setCheckinDate(date)}
                        />
                    </div>
                    <Row gutter={16} style={{ marginTop: "16px" }}>
                        <ButtonAmt
                            ct={"Vé người lớn"}
                            price={["200.000", "160.000"]}
                            setCnt={setCnt1}
                        />
                        <ButtonAmt
                            ct={
                                "Vé học sinh, sinh viên (giảm 50% vé người lớn)"
                            }
                            price={["160.000", "80.000"]}
                            setCnt={setCnt2}
                        />
                    </Row>
                    <div style={{ textAlign: "right" }}>
                        <span>Tổng tiền: </span>
                        <b style={{ color: "red" }}>
                            {(cnt1 * 160000 + cnt2 * 80000).toLocaleString(
                                "vi-VN"
                            )}{" "}
                            đ
                        </b>
                        <br />
                        <div>
                            <Button
                                style={{ marginTop: "10px" }}
                                danger
                                type="primary"
                                onClick={checkingData}
                            >
                                Đặt ngay
                            </Button>
                        </div>
                    </div>
                    <Modal
                        title="XÁC NHẬN ĐƠN HÀNG"
                        visible={isModalVisible}
                        onOk={handleOk}
                        onCancel={handleCancel}
                        okText="Xác nhận"
                        cancelText="Thoát"
                        style={{ textAlign: "center" }}
                        bodyStyle={{ textAlign: "left" }}
                    >
                        <b>Địa điểm: </b>
                        {locationDetails[location]}
                        <br />
                        <b>Ngày checkin: </b> {checkinDate}
                        <br />
                        <b>Số vé người lớn: </b> {cnt1}
                        <br />
                        <b>Số vé học sinh(sinh viên): </b> {cnt2}
                        <br />
                        <b>Mã đơn hàng: </b>
                        {codedh}
                    </Modal>
                </Card>
            )}
        </>
    );
};

export default Register;
